<template>
  <div class="page-container">
    <div class="page-section">
      <div class="table-wrapper">
        <h3 style="grid-column: span 2; margin-bottom: 0; margin-top: 0;">New CM WO Management</h3>

        <div class="input-wrapper">
          <div class="title-wrapper">
            <span>Date</span>
            <span>*</span>
          </div>
          <div class="select">
            <DxDateBox
              type="date"
              v-model="cmManagementList.report_date"
              placeholder="Select Date"
            />
          </div>
        </div>

        <div class="input-wrapper">
          <div class="title-wrapper">
            <span>Opened</span>
            <span>*</span>
          </div>
          <div class="input">
            <DxTextBox
              placeholder="Enter Opened"
              v-model="cmManagementList.tag_number"
            />
          </div>
        </div>

        <div class="input-wrapper">
          <div class="title-wrapper">
            <span>Closed</span>
            <span>*</span>
          </div>
          <div class="input">
            <DxTextBox
              placeholder="Enter Closed"
              v-model="cmManagementList.tag_number"
            />
          </div>
        </div>

        <div class="input-wrapper">
          <div class="title-wrapper">
            <span>Total</span>
            <span>*</span>
          </div>
          <div class="input">
            <DxTextBox
              placeholder="Enter Total"
              v-model="cmManagementList.tag_number"
            />
          </div>
        </div>

        <div class="input-wrapper" fill>
          <span>Note</span>
          <div class="input">
            <DxTextBox
              placeholder="Enter Note"
              v-model="cmManagementList.tag_number"
            />
          </div>
        </div>

        <button class="create" @click="CREATE_RECORD">Create</button>
        <button @click="SET_CURRENT_VIEW(0)">Cancel</button>

        <br>
      </div>
    </div>
  </div>
</template> 

<script>
//API
import { axios } from "/axios.js";
import moment from "moment";

//Components
//import VueTabsChrome from "vue-tabs-chrome";

//DataGrid
import "devextreme/dist/css/dx.light.css";
// import { Workbook } from "exceljs";
// import saveAs from "file-saver";
// import { exportDataGrid } from "devextreme/excel_exporter";
// import DxSelectBox from 'devextreme-vue/select-box';
import DxTextBox from 'devextreme-vue/text-box';
import DxDateBox from 'devextreme-vue/date-box';
// import DxTextArea from 'devextreme-vue/text-area';
// import DxAddRowButton from "devextreme-vue/button";
// import { DxItem } from "devextreme-vue/form";
import {
  // DxHtmlEditor,
  // DxToolbar,
  // DxMediaResizing,
  // //DxImageUpload,
  // DxItem
} from "devextreme-vue/html-editor";

import {
  // DxDataGrid,
  // DxSearchPanel,
  // DxPaging,
  // DxPager,
  // DxScrolling,
  // DxColumn,
  // DxExport,
  // DxToolbar,
  // DxHeaderFilter,
  // DxSelection,
  // DxEditing,
  // DxFilterRow,
  // DxLookup,
  // DxRequiredRule,
  // DxFormItem,
  // DxForm
} from "devextreme-vue/data-grid";

//Structures

export default {
  name: "inspection-record",
  components: {
    // DxDataGrid,
    // DxSearchPanel,
    // DxPaging,
    // DxPager,
    // DxScrolling,
    // DxColumn,
    // DxExport,
    // DxToolbar,
    // DxHeaderFilter,
    // DxSelection,
    // DxForm,
    // DxItem,
    // DxEditing,
    // DxFilterRow,
    // DxAddRowButton,
    // DxLookup,
    // DxRequiredRule,
    // DxFormItem,
    // DxSelectBox,
    DxTextBox,
    DxDateBox,
    // DxTextArea,
    // DxHtmlEditor,
    // DxToolbar,
    // DxMediaResizing,
    // DxItem
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "CM WO MANAGEMENT",
      subpageInnerName: null,
    });
    this.FETCH_DROPDOWN_ASSET();
    this.FETCH_DROPDOWN_PLATFORM();
  },
  data() {
    return {
      cmManagementList: {
        activities: null,
        details: null,
        id: 0,
        id_asset: null,
        id_platform: null,
        id_tag: 1,
        report_date: null,
        is_active: true,
        tag_number: null,
      },
      editorValue: null,
      formSelect: {
        asset: [],
        platform: [],
      },
      headerValues: [false, 1, 2, 3, 4, 5],
      headerOptions: { inputAttr: { 'aria-label': 'Header' } },
    };
  },
  computed: {},
  methods: {
    FETCH_INSP_RECORD() {
      this.isLoading = true;
      var id_tag = this.$route.params.id_tag;
      axios({
        method: "get",
        url:
          "/PipingInspectionRecord/get-piping-ir-by-id-line?id_line=" + id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          console.log("insp record:");
          console.log(res);
          if (res.status == 200 && res.data) {
            console.log("success");
            this.inspRecordList = res.data;
            console.log(this.inspRecordList);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    CREATE_RECORD() {
      if (this.cmManagementList.id_platform === null || 
          this.cmManagementList.id_asset === null || 
          this.cmManagementList.id_tag === null || 
          this.cmManagementList.report_date === null) 
          return;
      this.cmManagementList.report_date = moment(this.cmManagementList.report_date).format("L");
      axios({
        method: "post",
        url: "/HighlightActivities",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: this.cmManagementList
      })
        .then(res => {
          if (res.status == 201) {
            this.SET_CURRENT_VIEW(0);
          }
        })
        .catch(error => {
          this.$ons.notification.alert(
            error.code + " " + error.response.status + " " + error.message
          );
        })
        .finally(() => {});
    },
    FETCH_DROPDOWN_ASSET() {
      axios({
        method: "get",
        url: "/Md/get-md-asset-type-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.formSelect.asset = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_DROPDOWN_PLATFORM() {
      axios({
        method: "get",
        url: "/Md/get-md-platform-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.formSelect.platform = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    SET_CURRENT_VIEW(view, data = null) {
        this.$store.commit("SET_SHOW_BACK_BUTTON", true);
        if (data !== null) this.$emit('currentView', view, data);
        else this.$emit('currentView', view);
    }
  }
};
</script>

